@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Yanone+Kaffeesatz&display=swap);
.box{
   
    width: 32rem;
    margin: auto;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    min-height: 6rem;
    margin-top: 3rem;
    border-radius: 1rem;
    padding-top: 1rem;
    background-color: rgb(255,99,71,.6); 
    box-shadow: 5px 5px 15px -3px #000000;
    transition: all .1s ease;
}
.box input{
    padding-left: 1rem;
    width: 30rem;
    height: 4rem;
    font-size: 2rem;
    background-color: rgb(255,99,71,.3);
    border: transparent;
    color: #26241f;
    border-radius: 1rem;
}
.box input:focus{
    outline: none;
}
.icon{
    width: 100%;
    position: relative;
    height: 6rem;
}
.btn{
    position: absolute;
    right: 0;
    bottom: 0;
    color:#2E8B57
}
.add-btn{
    right: 0;
    bottom: 0;
}
.close-btn{
    left: 1rem;
    top: .5rem;
    color: #424242;
}
.list{
    width: 90%;
    margin: auto;
    padding-top: 10rem;
    padding-bottom: 10rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.main{
    min-width: 20rem;
    max-width: 20rem;
    margin-right: 3rem;
    margin-top: 3rem;
    background-image: linear-gradient(100deg,#96deda , #50c9c3);
    color:#424242;
    position: relative;
    padding-bottom: 1.5rem;
    overflow-wrap: break-word;


}

.del-btn{
    position: absolute;
    color: rgb(255,0,0,.9);
    bottom: .5rem;
    right: .5rem;
}
.item-name{
    font-size:2rem;
    text-transform: capitalize;
}

@media(max-width:528px){
    .main{
        width: 100%;
    }

}
.header{
    width: 100%;
    background-image: linear-gradient(80deg,#c79081 , #dfa579);
    color: white;
}
.header h1{
    font-family: 'Berkshire Swash', cursive;
    padding: 1rem;
    font-size: 3rem;
    padding-left: 8rem;
    text-align: justify;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
font-size: 10px;
font-family: 'Yanone Kaffeesatz', sans-serif;
}

